'use client';

import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
  CardDescription,
} from '@/components/ui/card';
import { Slider } from '@/components/ui/slider';
import {
  ArrowLeft,
  Sparkles,
  HeartCrack,
  HeartPulse,
  ArrowRight,
  Vote,
  Users,
  ShieldCheck,
  BarChart,
} from 'lucide-react';
import { cn } from '@/lib/utils';
import placeholderImages from '@/lib/placeholder-images.json';

type Vitals = {
  bloodPressure: number;
  cholesterol: number;
  heartRate: number;
  bloodSugar: number;
};

type TreeNodeProps = {
  condition: string;
  isPath?: boolean;
  isLeaf?: boolean;
  result?: 'Risky' | 'Not Risky';
  children?: React.ReactNode;
};

const TreeNode = ({
  condition,
  isPath,
  isLeaf,
  result,
  children,
}: TreeNodeProps) => (
  <div className="flex flex-col items-center relative">
    <div
      className={cn(
        'p-1.5 rounded-md border-2 bg-card text-card-foreground shadow-sm transition-all duration-500 min-w-[90px] text-center text-xs relative',
        {
          'border-primary bg-primary/10': isPath,
          'border-destructive bg-destructive/10':
            isLeaf && result === 'Risky' && isPath,
          'border-green-500 bg-green-500/10':
            isLeaf && result === 'Not Risky' && isPath,
        }
      )}
    >
      <p className="font-semibold">{isLeaf ? result : condition}</p>
    </div>
    {children && (
      <div className="flex justify-center mt-8 space-x-4 relative">
        {React.Children.map(children, (child, index) => (
          <div className="relative flex flex-col items-center">
            <div
              className={cn(
                'absolute w-px bg-border -top-8 h-8',
                (React.Children.count(children) === 1 ||
                  (index === 0 && React.Children.count(children) > 1)) &&
                  'left-1/2 -translate-x-1/2',
                index === 1 && 'right-1/2 translate-x-1/2'
              )}
            ></div>
            <div
              className={cn(
                'absolute w-1/2 h-px bg-border -top-8',
                index === 0 && 'left-1/2',
                index === 1 && 'right-1/2'
              )}
            ></div>
            {child}
          </div>
        ))}
      </div>
    )}
  </div>
);

type DecisionTreeProps = {
  vitals: Vitals | null;
  treeId: number;
  isActive: boolean;
};

const DecisionTree = ({ vitals, treeId, isActive }: DecisionTreeProps) => {
  const [paths, setPaths] = React.useState<string[]>([]);
  const [result, setResult] = React.useState<'Risky' | 'Not Risky' | null>(
    null
  );

  React.useEffect(() => {
    if (isActive && vitals) {
      const { bloodPressure, cholesterol, heartRate, bloodSugar } = vitals;
      const bpSys = bloodPressure;
      const chol = cholesterol;
      const hr = heartRate;
      const bs = bloodSugar;
      let currentPath: string[] = ['root'];
      let treeResult: 'Risky' | 'Not Risky';

      if (treeId === 1) {
        if (bpSys > 140) {
          currentPath.push('bp>140');
          if (chol > 240) {
            currentPath.push('bp>140_chol>240');
            treeResult = 'Risky';
          } else {
            currentPath.push('bp>140_chol<=240');
            treeResult = 'Risky';
          }
        } else {
          currentPath.push('bp<=140');
          if (hr > 90) {
            currentPath.push('bp<=140_hr>90');
            treeResult = 'Risky';
          } else {
            currentPath.push('bp<=140_hr<=90');
            treeResult = 'Not Risky';
          }
        }
      } else if (treeId === 2) {
        if (chol > 220) {
          currentPath.push('chol>220');
          if (bs > 125) {
            currentPath.push('chol>220_bs>125');
            treeResult = 'Risky';
          } else {
            currentPath.push('chol>220_bs<=125');
            treeResult = 'Not Risky';
          }
        } else {
          currentPath.push('chol<=220');
          if (bpSys > 130) {
            currentPath.push('chol<=220_bp>130');
            treeResult = 'Risky';
          } else {
            currentPath.push('chol<=220_bp<=130');
            treeResult = 'Not Risky';
          }
        }
      } else {
        if (hr > 85) {
          currentPath.push('hr>85');
          if (bs > 110) {
            currentPath.push('hr>85_bs>110');
            treeResult = 'Risky';
          } else {
            currentPath.push('hr>85_bs<=110');
            treeResult = 'Not Risky';
          }
        } else {
          currentPath.push('hr<=85');
          if (chol > 200) {
            currentPath.push('hr<=85_chol>200');
            treeResult = 'Risky';
          } else {
            currentPath.push('hr<=85_chol<=200');
            treeResult = 'Not Risky';
          }
        }
      }
      setPaths(currentPath);
      setResult(treeResult);
    } else if (!isActive) {
      setPaths([]);
      setResult(null);
    }
  }, [isActive, vitals, treeId]);

  const isPath = (id: string) => isActive && paths.includes(id);

  const renderTree = () => {
    if (treeId === 1) {
      return (
        <TreeNode condition="BP > 140?" isPath={isPath('root')}>
          <div className="flex flex-col items-center">
            <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
            <TreeNode condition="Cholesterol > 240?" isPath={isPath('bp>140')}>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
                <TreeNode
                  isLeaf
                  result="Risky"
                  isPath={isPath('bp>140_chol>240')}
                />
              </div>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
                <TreeNode
                  isLeaf
                  result="Risky"
                  isPath={isPath('bp>140_chol<=240')}
                />
              </div>
            </TreeNode>
          </div>
          <div className="flex flex-col items-center">
            <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
            <TreeNode condition="Heart Rate > 90?" isPath={isPath('bp<=140')}>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
                <TreeNode
                  isLeaf
                  result="Risky"
                  isPath={isPath('bp<=140_hr>90')}
                />
              </div>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
                <TreeNode
                  isLeaf
                  result="Not Risky"
                  isPath={isPath('bp<=140_hr<=90')}
                />
              </div>
            </TreeNode>
          </div>
        </TreeNode>
      );
    }

    if (treeId === 2) {
      return (
        <TreeNode condition="Cholesterol > 220?" isPath={isPath('root')}>
          <div className="flex flex-col items-center">
            <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
            <TreeNode
              condition="Blood Sugar > 125?"
              isPath={isPath('chol>220')}
            >
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
                <TreeNode
                  isLeaf
                  result="Risky"
                  isPath={isPath('chol>220_bs>125')}
                />
              </div>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
                <TreeNode
                  isLeaf
                  result="Not Risky"
                  isPath={isPath('chol>220_bs<=125')}
                />
              </div>
            </TreeNode>
          </div>
          <div className="flex flex-col items-center">
            <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
            <TreeNode condition="BP > 130?" isPath={isPath('chol<=220')}>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
                <TreeNode
                  isLeaf
                  result="Risky"
                  isPath={isPath('chol<=220_bp>130')}
                />
              </div>
              <div className="flex flex-col items-center">
                <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
                <TreeNode
                  isLeaf
                  result="Not Risky"
                  isPath={isPath('chol<=220_bp<=130')}
                />
              </div>
            </TreeNode>
          </div>
        </TreeNode>
      );
    }

    return (
      <TreeNode condition="Heart Rate > 85?" isPath={isPath('root')}>
        <div className="flex flex-col items-center">
          <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
          <TreeNode condition="Blood Sugar > 110?" isPath={isPath('hr>85')}>
            <div className="flex flex-col items-center">
              <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
              <TreeNode
                isLeaf
                result="Risky"
                isPath={isPath('hr>85_bs>110')}
              />
            </div>
            <div className="flex flex-col items-center">
              <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
              <TreeNode
                isLeaf
                result="Not Risky"
                isPath={isPath('hr>85_bs<=110')}
              />
            </div>
          </TreeNode>
        </div>
        <div className="flex flex-col items-center">
          <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
          <TreeNode condition="Cholesterol > 200?" isPath={isPath('hr<=85')}>
            <div className="flex flex-col items-center">
              <span className="text-xs mb-1 absolute -top-5 left-0">Yes</span>
              <TreeNode
                isLeaf
                result="Risky"
                isPath={isPath('hr<=85_chol>200')}
              />
            </div>
            <div className="flex flex-col items-center">
              <span className="text-xs mb-1 absolute -top-5 right-0">No</span>
              <TreeNode
                isLeaf
                result="Not Risky"
                isPath={isPath('hr<=85_chol<=200')}
              />
            </div>
          </TreeNode>
        </div>
      </TreeNode>
    );
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-center text-lg">Tree {treeId}</CardTitle>
      </CardHeader>
      <CardContent className="flex justify-center p-4 min-h-[220px] items-center">
        {renderTree()}
      </CardContent>
      {result && (
        <CardFooter className="flex justify-center text-sm font-medium">
          Tree {treeId} Voted:{' '}
          <span
            className={cn('font-bold ml-1', {
              'text-destructive': result === 'Risky',
              'text-green-500': result === 'Not Risky',
            })}
          >
            {result}
          </span>
        </CardFooter>
      )}
    </Card>
  );
};

export default function PredictPage() {
  const [vitals, setVitals] = React.useState<Vitals>({
    bloodPressure: 120,
    cholesterol: 200,
    heartRate: 75,
    bloodSugar: 99,
  });
  const [vitalsForPrediction, setVitalsForPrediction] =
    React.useState<Vitals | null>(null);
  const [isMounted, setIsMounted] = React.useState(false);
  const [isPredicting, setIsPredicting] = React.useState(false);
  const [prediction, setPrediction] = React.useState<
    'Risky' | 'Not Risky' | null
  >(null);
  const [treeResults, setTreeResults] = React.useState<
    ('Risky' | 'Not Risky')[]
  >([]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  const handleInputChange = (field: keyof Vitals, value: number) => {
    setVitals((prev) => ({ ...prev, [field]: value }));
    if (prediction) {
      reset();
    }
  };

  const handlePredict = () => {
    setIsPredicting(true);
    setVitalsForPrediction(vitals);
  };

  React.useEffect(() => {
    if (isPredicting && vitalsForPrediction) {
      const timeoutId = setTimeout(() => {
        const results: ('Risky' | 'Not Risky')[] = [];
        const { bloodPressure, cholesterol, heartRate, bloodSugar } =
          vitalsForPrediction;
        const bpSys = bloodPressure;
        const chol = cholesterol;
        const hr = heartRate;
        const bs = bloodSugar;

        // Tree 1 logic
        if (bpSys > 140) results.push('Risky');
        else if (hr > 90) results.push('Risky');
        else results.push('Not Risky');

        // Tree 2 logic
        if (chol > 220 && bs > 125) results.push('Risky');
        else if (chol <= 220 && bpSys > 130) results.push('Risky');
        else if (chol > 220 && bs <= 125) results.push('Not Risky');
        else results.push('Not Risky');

        // Tree 3 logic
        if (hr > 85 && bs > 110) results.push('Risky');
        else if (hr <= 85 && chol > 200) results.push('Risky');
        else if (hr > 85 && bs <= 110) results.push('Not Risky');
        else results.push('Not Risky');

        setTreeResults(results);

        const riskyCount = results.filter((r) => r === 'Risky').length;
        const finalPrediction = riskyCount >= 2 ? 'Risky' : 'Not Risky';

        setPrediction(finalPrediction);
        setIsPredicting(false);
      }, 1500); // Wait for animations to play

      return () => clearTimeout(timeoutId);
    }
  }, [isPredicting, vitalsForPrediction]);

  const reset = () => {
    setPrediction(null);
    setTreeResults([]);
    setVitalsForPrediction(null);
  };

  const allFieldsFilled =
    vitals.bloodPressure &&
    vitals.cholesterol &&
    vitals.heartRate &&
    vitals.bloodSugar;

  if (!isMounted) {
    return null;
  }

  return (
    <div className="flex flex-col min-h-screen bg-background">
      <header className="sticky top-0 z-30 flex items-center h-16 px-4 border-b bg-background/80 backdrop-blur-sm">
        <Link href="/doctor">
          <Button variant="ghost" size="icon">
            <ArrowLeft className="w-5 h-5" />
          </Button>
        </Link>
        <h1 className="ml-4 text-xl font-semibold">
          Heart Attack Prediction
        </h1>
      </header>
      <main className="flex-1 p-4 md:p-6 flex flex-col items-center">
        <div className="w-full max-w-2xl mx-auto">
          <div className="text-center mb-8">
            <h2 className="text-3xl font-bold tracking-tight text-foreground">
              Patient Vitals Input
            </h2>
            <p className="mt-2 text-lg text-muted-foreground">
              Enter the patient's vitals to predict the risk of a heart attack.
            </p>
          </div>
          <div
            className="relative flex justify-center items-center"
            style={{ minHeight: '400px' }}
          >
            <Image
              src={placeholderImages.human_body_scan.src}
              alt="Human Body"
              width={400}
              height={400}
              data-ai-hint={placeholderImages.human_body_scan['data-ai-hint']}
              className="object-contain"
            />
            <div className="absolute top-8 right-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className="p-3">
                  <CardTitle className="text-sm">Blood Pressure (BP)</CardTitle>
                </CardHeader>
                <CardContent className="p-3 pt-0">
                  <div className="flex items-center justify-between">
                    <span className="text-xs font-medium">
                      {vitals.bloodPressure}
                    </span>
                  </div>
                  <Slider
                    id="bp"
                    min={80}
                    max={200}
                    step={1}
                    value={[vitals.bloodPressure]}
                    onValueChange={(value) =>
                      handleInputChange('bloodPressure', value[0])
                    }
                    className="mt-2"
                  />
                </CardContent>
              </Card>
            </div>
            <div className="absolute top-8 left-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className="p-3">
                  <CardTitle className="text-sm">Cholesterol</CardTitle>
                </CardHeader>
                <CardContent className="p-3 pt-0">
                  <div className="flex items-center justify-between">
                    <span className="text-xs font-medium">
                      {vitals.cholesterol}
                    </span>
                  </div>
                  <Slider
                    id="chol"
                    min={150}
                    max={300}
                    step={1}
                    value={[vitals.cholesterol]}
                    onValueChange={(value) =>
                      handleInputChange('cholesterol', value[0])
                    }
                    className="mt-2"
                  />
                </CardContent>
              </Card>
            </div>
            <div className="absolute bottom-8 left-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className="p-3">
                  <CardTitle className="text-sm">Heart Rate</CardTitle>
                </CardHeader>
                <CardContent className="p-3 pt-0">
                  <div className="flex items-center justify-between">
                    <span className="text-xs font-medium">
                      {vitals.heartRate}
                    </span>
                  </div>
                  <Slider
                    id="hr"
                    min={50}
                    max={120}
                    step={1}
                    value={[vitals.heartRate]}
                    onValueChange={(value) =>
                      handleInputChange('heartRate', value[0])
                    }
                    className="mt-2"
                  />
                </CardContent>
              </Card>
            </div>
            <div className="absolute bottom-8 right-0">
              <Card className="w-36 shadow-lg transition-all duration-300 hover:shadow-primary/20 hover:shadow-xl">
                <CardHeader className="p-3">
                  <CardTitle className="text-sm">Blood Sugar</CardTitle>
                </CardHeader>
                <CardContent className="p-3 pt-0">
                  <div className="flex items-center justify-between">
                    <span className="text-xs font-medium">
                      {vitals.bloodSugar}
                    </span>
                  </div>
                  <Slider
                    id="bs"
                    min={70}
                    max={180}
                    step={1}
                    value={[vitals.bloodSugar]}
                    onValueChange={(value) =>
                      handleInputChange('bloodSugar', value[0])
                    }
                    className="mt-2"
                  />
                </CardContent>
              </Card>
            </div>
          </div>
          <div className="flex justify-center mt-8">
            <Button
              size="lg"
              onClick={handlePredict}
              disabled={!allFieldsFilled || isPredicting}
            >
              <Sparkles className="w-5 h-5 mr-2" />
              {isPredicting
                ? 'Analyzing...'
                : prediction
                  ? 'Run New Prediction'
                  : 'Run Prediction'}
            </Button>
          </div>
        </div>

        {(isPredicting || prediction) && (
          <div className="flex flex-col items-center w-full mt-12">
            <h2 className="text-2xl font-bold mb-6">
              {prediction ? 'Prediction Details' : 'Analyzing Patient Data...'}
            </h2>

            <div
              className={cn(
                'grid grid-cols-1 lg:grid-cols-3 gap-8 w-full max-w-5xl transition-opacity duration-1000',
                isPredicting || prediction ? 'opacity-100' : 'opacity-0'
              )}
            >
              {[1, 2, 3].map((treeId) => (
                <DecisionTree
                  key={treeId}
                  vitals={vitalsForPrediction}
                  treeId={treeId}
                  isActive={!!vitalsForPrediction}
                />
              ))}
            </div>

            {prediction && (
              <>
                <Card className="mt-12 w-full max-w-3xl">
                  <CardHeader>
                    <CardTitle className="flex items-center justify-center gap-2">
                      <Vote className="w-6 h-6" />
                      How the Final Prediction is Made
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="flex flex-col items-center justify-center p-6 space-y-6">
                    <div className="flex items-center space-x-8">
                      {treeResults.map((result, index) => (
                        <div
                          key={index}
                          className="flex flex-col items-center space-y-2"
                        >
                          <div
                            className={cn(
                              'w-16 h-16 rounded-full flex items-center justify-center text-white font-bold text-lg shadow-lg',
                              result === 'Risky'
                                ? 'bg-destructive'
                                : 'bg-green-500'
                            )}
                          >
                            {result === 'Risky' ? (
                              <HeartCrack className="w-8 h-8" />
                            ) : (
                              <HeartPulse className="w-8 h-8" />
                            )}
                          </div>
                          <span className="text-sm font-medium text-muted-foreground">
                            Tree {index + 1}
                          </span>
                          <span
                            className={cn('font-bold ml-1 text-sm', {
                              'text-destructive': result === 'Risky',
                              'text-green-500': result === 'Not Risky',
                            })}
                          >
                            {result}
                          </span>
                        </div>
                      ))}
                    </div>

                    <ArrowRight className="w-12 h-12 text-muted-foreground rotate-90 md:rotate-0" />

                    <div className="flex flex-col items-center space-y-2">
                      <div
                        className={cn(
                          'w-24 h-24 rounded-full flex items-center justify-center text-white font-bold text-2xl shadow-xl',
                          prediction === 'Risky'
                            ? 'bg-destructive'
                            : 'bg-green-500'
                        )}
                      >
                        {prediction === 'Risky' ? (
                          <HeartCrack className="w-12 h-12" />
                        ) : (
                          <HeartPulse className="w-12 h-12" />
                        )}
                      </div>
                      <span className="text-lg font-semibold">
                        Final Result:{' '}
                        <span
                          className={cn({
                            'text-destructive': prediction === 'Risky',
                            'text-green-500': prediction === 'Not Risky',
                          })}
                        >
                          {prediction}
                        </span>
                      </span>
                    </div>
                  </CardContent>
                </Card>

                <Card className="mt-8 w-full max-w-3xl">
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <Sparkles className="w-6 h-6 text-primary" />
                      Why Random Forest Works
                    </CardTitle>
                    <CardDescription>
                      The "Random Forest" method provides a more reliable
                      prediction by combining multiple decision trees.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="grid grid-cols-1 md:grid-cols-3 gap-6 text-center">
                    <div className="flex flex-col items-center space-y-2">
                      <div className="p-3 rounded-full bg-primary/10">
                        <Users className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="font-semibold">Multiple Perspectives</h3>
                      <p className="text-sm text-muted-foreground">
                        Each tree votes, providing a broader analysis than a
                        single opinion.
                      </p>
                    </div>
                    <div className="flex flex-col items-center space-y-2">
                      <div className="p-3 rounded-full bg-primary/10">
                        <BarChart className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="font-semibold">Reduces Errors</h3>
                      <p className="text-sm text-muted-foreground">
                        Averaging many trees cancels out individual errors.
                      </p>
                    </div>
                    <div className="flex flex-col items-center space-y-2">
                      <div className="p-3 rounded-full bg-primary/10">
                        <ShieldCheck className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="font-semibold">Higher Accuracy</h3>
                      <p className="text-sm text-muted-foreground">
                        The collective decision is more accurate and reliable,
                        leading to better patient outcomes.
                      </p>
                    </div>
                  </CardContent>
                  <CardFooter>
                    <p
                      className={cn(
                        'w-full text-center font-semibold text-lg',
                        {
                          'text-destructive': prediction === 'Risky',
                          'text-green-500': prediction === 'Not Risky',
                        }
                      )}
                    >
                      {prediction === 'Risky'
                        ? 'This collective approach gives higher confidence in the "Risky" prediction.'
                        : 'This collective approach confirms the patient is likely not at immediate risk.'}
                    </p>
                  </CardFooter>
                </Card>
              </>
            )}
          </div>
        )}
      </main>
    </div>
  );
}
